@import '../../../../assets/styles/scss/data.scss';

#Contact{
  max-width: 900px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
  margin-top: -50px;

  @media ( max-width: 991px ) {
    margin-top: -180px;
  }

  @media ( max-height: 750px ) {
    margin-bottom: 250px;
  }

  h1{
    color: #fff;
    font-family: $text-hi;
    font-weight: 700 !important;
    margin-bottom: 40px;
  }

  .alert{
    text-align: center;
    padding: 5px;
    margin-bottom: 30px;
    border: none;
    color: #fff;
    font-family: $header-text;
  }

  .alert-danger{
    background: red;
  }

  .alert-success{
    background: rgb(55, 109, 55);
  }



  label{
    color: #fff;
    font-family: $header-text;
    font-weight: 700 !important;
    text-transform: uppercase;

    span{
      text-transform: none;
      padding-left: 15px;
      font-size: 13px;
    }
  }

  input, textarea{
    margin-bottom: 30px;
    background: transparent !important;
    color: #fff;

    &:focus{
      border-color: rgb(250, 152, 116);
      box-shadow: none !important;
    }
  }

  .input-err{
    border-color: red !important;
  }

  ::placeholder {
    color: #bbb;
    font-family: $text-hi;
    letter-spacing: 1.5px;
  }

  :-ms-input-placeholder{
    color: #bbb;
    font-family: $text-hi;
    letter-spacing: 1.5px;
  }

  ::-ms-input-placeholder{
    color: #bbb;
    font-family: $text-hi;
    letter-spacing: 1.5px;
  }

  textarea{
    height: 150px;
  }

  button{
    margin-top: 60px;
    font-family: $header-text;
    font-weight: 700 !important;
    background: rgba(255, 0, 0, 0.25) !important;
    border: none;
    box-shadow: none !important;
    transition: .5s ease-in;

    &:hover{
      background: rgba(255, 0, 0, 0.5) !important;
    }
  }

  .btn-err{

    &:hover{
      cursor: not-allowed;
      background: rgba(255, 0, 0, 0.25) !important;
    }
  }
}