@import url(https://fonts.googleapis.com/css?family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alegreya:400,500,700&display=swap);
#Contact{max-width:900px;padding:0 15px;margin:0 auto;position:relative;margin-top:-50px}@media (max-width: 991px){#Contact{margin-top:-180px}}@media (max-height: 750px){#Contact{margin-bottom:250px}}#Contact h1{color:#fff;font-family:"Open Sans Condensed",sans-serif;font-weight:700 !important;margin-bottom:40px}#Contact .alert{text-align:center;padding:5px;margin-bottom:30px;border:none;color:#fff;font-family:"Open Sans",sans-serif}#Contact .alert-danger{background:red}#Contact .alert-success{background:#376d37}#Contact label{color:#fff;font-family:"Open Sans",sans-serif;font-weight:700 !important;text-transform:uppercase}#Contact label span{text-transform:none;padding-left:15px;font-size:13px}#Contact input,#Contact textarea{margin-bottom:30px;background:transparent !important;color:#fff}#Contact input:focus,#Contact textarea:focus{border-color:#fa9874;box-shadow:none !important}#Contact .input-err{border-color:red !important}#Contact ::-webkit-input-placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact :-ms-input-placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact ::-ms-input-placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact ::placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact :-ms-input-placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact ::-ms-input-placeholder{color:#bbb;font-family:"Open Sans Condensed",sans-serif;letter-spacing:1.5px}#Contact textarea{height:150px}#Contact button{margin-top:60px;font-family:"Open Sans",sans-serif;font-weight:700 !important;background:rgba(255,0,0,0.25) !important;border:none;box-shadow:none !important;-webkit-transition:.5s ease-in;transition:.5s ease-in}#Contact button:hover{background:rgba(255,0,0,0.5) !important}#Contact .btn-err:hover{cursor:not-allowed;background:rgba(255,0,0,0.25) !important}

